
import { defineComponent, ref } from 'vue';
import { Textarea } from '@arco-design/web-vue';
import { IconSend, IconDelete } from '@arco-design/web-vue/es/icon';
import { useRouter } from 'vue-router';
import axios from 'axios';


export default defineComponent({
   name: 'MessageDisplay',

   components: { IconSend,
      'a-textarea': Textarea,
    },
   data() {
  return {
   textareaValue: Textarea,
    isZoomed: false
  }
},

setup() {
    const router = useRouter() // useRouterrouter

    const goToshuju = () => {
         router.push('/shuju') // router
      }
      const goTogpt = () => {
         router.push('/chat') // router
      }
      const goTospark = () => {
         router.push('/spark') // router
      }
const messages = ref([
         {
            id: 1,
            content: '',
         }
      ]);
      const user_input = ref('');
      const chat_name = ref('newchat'); 


      const newChat = async () => {
         const new_chat = "True";
         try{
            console.log("")
            console.log(new_chat)
            axios.post('http://81.68.164.228/api/LinChanceGPT/chat_with_Faiss/', { new_chat });
         }
         catch (error) {
               console.error('', error);
            }

      }
      const sendMessage = async () => {
         //user_input,if
         if (user_input.value) {
            const newMessage = {
               //newMessageidcontent
               id: messages.value.length + 1,
               //newMessageidmessages.value.length + 1
               //messages1
               content: user_input.value,
               //user_input
            };
            console.log(user_input.value)
            //
            messages.value.push(newMessage);
            console.log("messages")
            console.log(messages.value)
            //newMessagemessages
            const question = user_input.value;
            //user_inputquestion
            console.log("")
            const chat_name = ""
            const new_chat = "False"
            const postdata={
               user_input:question,
               chat_name:chat_name,
               new_chat:new_chat,
            }
            try {
               const gpt_response = await axios.post('http://81.68.164.228/api/LinChanceGPT/chat_with_Faiss/', { postdata});

               const answer = gpt_response.data[0];
               console.log(gpt_response.data[0])
               //answer

               const newMessage = {
                  //newMessageidmessages.value.length + 1
               //messages1
                  content: answer,
                  id: messages.value.length + 1,
               };


               // 


               messages.value.push(newMessage);
               console.log(messages.value)
            } 

            catch (error) {
               console.error('', error);
            }

         }
      };


      const history = ref([
         {
            timeStamp: '2021-10-10 10:10:10',
            chat_name: 'ChatGPT',
         },
          {
            timeStamp: '2021-10-10 10:10:10',
            chat_name: 'Spark',
         },
         {
            timeStamp: '2021-10-10 10:10:10',
            chat_name: '',
         }

      ]);

      const formattedTime = ref('');

      const messageCount = ref(2)

      const pad = (number: number) => {
         return number < 10 ? '0' + number : number;
      };

      const clickHistory = async (timeStamp: string) => {
         console.log('clickHistory', timeStamp);
         const timestamp = timeStamp;
         // const messsage = await axios.get('http://81.68.164.228/api/LinChanceGPT/chat_history', { timestamp });
         alert(timestamp);

      };

      // const deleteHistory = (timeStamp:string) => {
      //    console.log('deleteHistory',timeStamp);
      // };

      const getHistory = async () => {
         const res = await axios.get('http://81.68.164.228/api/LinChanceGPT/chat_history');
         history.value = res.data;
      };

      const createMessage = async () => {
         getCurrentTime();
         history.value.push({
            timeStamp: formattedTime.value,
            chat_name: '' + messageCount.value++,
         })
         try {
            const res = await axios.post('http://http://81.68.164.228/api/LinChanceGPT/chat_history', { timeStamp: formattedTime.value });
            console.log(res);
         }
         catch(error) {
            console.error('', error);
         }

      };

      const getCurrentTime = () => {
         const current = new Date();
         const year = current.getFullYear();
         const month = pad(current.getMonth() + 1);
         const day = pad(current.getDate());
         const hours = pad(current.getHours());
         const minutes = pad(current.getMinutes());
         const seconds = pad(current.getSeconds());
         formattedTime.value = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
      };

      return {
          newChat,
          messages,
          user_input,
          history,
          messageCount,
          sendMessage,
          goToshuju,
          goTogpt,
          goTospark,
          clickHistory,
          getHistory,
          createMessage,
          getCurrentTime,
          chat_name,

       };
   },
});

